const initialState = {
    check2: false,
    check3: false
  }
 export const check = (state = initialState, action) => {
      switch (action.type){
          case 'CHECK_BOX':
          console.warn("action done")
              return {
                  ...state,
                  check2: !state.check2,
              }
           case 'CHECK_BOX3':
                return {
                   ...state,
                   check3: !state.check3,
                   check2: !state.check2
               }        
          default:
              return state
      }
  }

  export default check